@mixin h0 {
  @include defaultTypoStyles(
    14,
    0,
    20,
    normal,
    var(--font-family-poppins-regular)
  );
  @include media('>=tablet') {
    @include defaultTypoStyles(
      20,
      0,
      32,
      normal,
      var(--font-family-poppins-regular)
    );
  }
}

